import Vue from 'vue'
import Router from 'vue-router'
const lazyLoading = (name) => (resolve) => require([`../pages/${name}`], resolve)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/commodity',
      name: 'home',
      component: lazyLoading('home/index'),
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      children: [
        { path: '/commodity', component: lazyLoading('commodity/commodityList'), name: 'commodity' },
        { path: '/commodityDetail', component: lazyLoading('commodity/commodityDetail'), name: 'commodityDetail' },
        { path: '/table4', component: lazyLoading('commodity/table4'), name: 'table4' }
      ]
    },
    {
      path: '/charts1',
      name: 'charts',
      component: lazyLoading('home/index'),
      children: [
        { path: '/charts1', component: lazyLoading('echarts/echarts1'), name: 'echarts1' }
      ]
    },
    {
      path: '/order',
      name: 'order',
      component: lazyLoading('home/index'),
      children: [
        { path: '/order', component: lazyLoading('order/orderList'), name: 'orderList' },
        { path: '/orderDetail', component: lazyLoading('order/orderDetail'), name: 'orderDetail' }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: lazyLoading('login')
    }
  ]
})



